import React from 'react';
import { Node } from '@ali/4ever-cangjie';
import getCodeHeight from '../../components/Codemirror/utils';
import { DEFAULT_THEME } from '../../utils/constants/themes';
import Placeholder from '../../components/Placeholder';
import Code from '../../mo/models';

export default function lazyRenderStrategy(_, node: Node) {
  if (Code.isCode(node)) {
    const height = getCodeHeight(node);
    const { theme = DEFAULT_THEME } = node.data;

    return {
      placeholder: (
        <Placeholder height={height} dark={theme !== DEFAULT_THEME} />
      )
    }
  }
  return undefined;
}
